.full{
    height: 100vh;
    background-color: black;
}

.boss{
    height: 50vh;
    text-align: center;
    border-bottom: 10px solid rgba(199,199,199,0.66);
    border-radius: 40px 40px 25px 25px;
    padding: 10px 10px;
}

.players{
    width: 100%;
    height: 50vh;
    text-align: center;
    display: flex;
    gap: 16px;
    padding-left: 40px;
    padding-right: 40px;
}

.player{
    padding: 10px 10px;
    flex-grow: 1;
    /*border: white solid 1px;*/
}

.player > img {
    height: 50%;
}

.center300 {
    text-align: center;
    padding-top: 250px;
}

.whiteText {
    color: white;
    font-size: 30px;
    padding-bottom: 15px;
    font-family: "Times New Roman";
}

.dmgText {
    color: white;
    font-size: 25px;
    padding-top: 5px;
    font-family: "Times New Roman";
}

.bossborder {
    margin-left: 100px;
    margin-right: 100px;
    border: 5px inset rgba(199,199,199,0.66);
    border-radius: 0px 40px 0px 29px;
}

.playerborder {
    margin-left: 20px;
    margin-right: 20px;
    border: 5px inset rgba(199,199,199,0.66);
    border-radius: 30px 0px 30px 0px;
}

.bossbar{
    padding-left: 400px;
    padding-right: 400px;
}

.centerText{
    text-align:center;
}

.battlelayout {
    width: 100%;
    height: 100%;

    display: grid;
    grid:
    "sidebar body" 1fr
    / 72% 1fr;
    gap: 8px;
}

.battle {
    grid-area: sidebar;
}

.log {
    border: 5px solid #A8A8A8;
    border-radius: 10px;
    grid-area: body;
}

.surrender {
    text-align: right;
    padding-right: 20px;
    padding-top: 25px;
    padding-bottom: 20px;

}

.logText {
    color: white;
    font-family: "Times New Roman";
    font-size: 20px;
    padding: 10px 10px;
    border-top: 5px solid #A8A8A8;
    border-radius: 40px 40px 0px 0px;
    height: 90%;
}

.surrenderbox {
    width: 100%;
    height: 10%;
    display: flex;
    gap: 16px;
    border-bottom: 5px solid #A8A8A8;
    border-radius: 40px 40px 40px 40px;
}

.itembox { flex-grow: 1; }

.logobox {
    text-align: center;
    padding: 10px;
}

.skillButton {
    height: 65px;
    width: 65px;
    color: black;
    background-color: black;
    border: black;
    padding-bottom: 30px;

}

.skillButton:disabled > img {
    -webkit-filter: grayscale(1);
}

.iconBorder {
    border: 2px solid #A8A8A8;
    border-radius: 10px;
}

.bossSkill {
    padding-bottom: 0px;
}

.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;
}

.tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: white;
    color: black;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 150%;
    left: 50%;
    margin-left: -60px;
}

.tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: white transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
}

.logLine {
    line-height: 80%;
    font-size: 16px;
}